.App {
  text-align: center;
}

.App-logo {
  background-image: "url("anything.png")";
  height: 20%;
  width: auto;
  pointer-events: none;
}

.Resize {
  background-image: "url("anything.png")";
  height: 30%;
  width: auto;
  pointer-events: none;
}

#Applink {
  margin-top: 132px;
  font-size: 60px;
  font-weight: 550;
  color: #909090;
  text-decoration: none;
  margin-bottom: 250px
}

#Applink:hover {
  color: #DDDDDD;
}

.App-link {
  color: #909090;
}

.App-link:hover {
  color: #DDDDDD;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 6s linear;
  }
}

.App-header {
  background-color: #730073;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
